import { IFontStyle, ITextSequenceBase } from "piress-core-shared";
import { cursor } from ".";

export interface IRenderOptions {
  seqs: ITextSequenceBase;
  effect: boolean;
  padding: number;
  width?: number;
  height?: number;
  shadow?: boolean | "fake";
  style: Partial<
    IFontStyle & {
      background?: string | CanvasGradient | CanvasPattern;
      fill?: string | CanvasGradient | CanvasPattern;
    }
  >;
}

const gradientMap = {} as Record<string, CanvasGradient>;
export function offscreenRender(data: IRenderOptions) {
  let {
    seqs: textSequence,
    effect,
    padding,
    width = textSequence.width,
    height = textSequence.height,
    style,
  } = data;
  if (padding > 0) {
    width += padding * 2;
    height += padding * 2;
  }

  const canvas = new OffscreenCanvas(width, height);
  const context = canvas.getContext("2d")!;

  if (style.background) {
    context.fillStyle = style.background;
    context.fillRect(0, 0, width, height);
  }
  // context.fillStyle = style.fill || "#000"

  // context.textBaseline = "ideographic";

  if (effect) {
    if (data.shadow === true) {
      context.shadowOffsetX = 2;
      context.shadowOffsetY = 2;
      context.shadowBlur = 2;
      context.shadowColor = "#333";
    }
    context.strokeStyle = "#333";
    context.lineWidth = 2;
  }

  for (const char of cursor(textSequence)) {
    let gradient!: CanvasGradient;
    const x = char.x + padding + 1,
      y = char.y + padding + char.height - 4;

    context.font = char.font; //toFontString(char, style);
    context.letterSpacing = char.style.letterSpacing + "px";
    // console.log(char, context.font, style)
    if (effect) {
      if (data.shadow === "fake") {
        context.fillStyle = "#aaa";
        context.strokeText(char.character, x + 1, y + 1);
        context.fillText(char.character, x + 1, y + 1);
      }
      context.strokeText(char.character, x, y);
      const key = `${y},${char.height}`;
      gradient = gradientMap[key];
      if (!gradient) {
        gradient = gradientMap[key] = context.createLinearGradient(
          0,
          y - char.height / 3 * 2,
          0,
          y,
        );
        gradient.addColorStop(0, "#ffffff");
        gradient.addColorStop(1, "#2de2ff");
      }

      context.fillStyle = gradient;
    }
    context.fillText(char.character, x, y);
  }
  return canvas.transferToImageBitmap();
}
