import { calc, create as createElm, ecs, element, printCache } from "piress-core-rescript";
import {
  Cache,
  CompressedElementIdentifiers,
  createElements,
  ElementIdentifier,
  getOptions,
  ITextSequenceBase,
  ITypographyOptions,
  optionCache,
  visitCompressedIds,
} from "piress-core-shared";
import { tokenize } from "./tokenize";

export declare class TextSequence implements ITextSequenceBase {
  seqs: CompressedElementIdentifiers;
  areaWidth: number;
  areaHeight: number;
  width: number;
  height: number;
  lastLineIndex: number;
  elements: element[];
}

export function* cursor(result: ITextSequenceBase | TextSequence) {
  if ("elements" in result) {
    for (const el of result.elements) {
      yield el;
    }
  } else {
    const ctx = result;
    for (const el of visitCompressedIds(ctx.seqs)) {
      if (ecs.getWidth(el) > 0) {
        // const cursor = ecs.cursor(el);
        yield ecs.cursor(el);
      }
    }
  }
}

export function output(result: ITextSequenceBase | TextSequence) {
  if ("elements" in result) return result.elements;
  const { seqs } = result;
  const elements = [] as element[];
  // 此处实现可见visitCompressedIds
  let lineNumber = 0,
    prev: ElementIdentifier = seqs[lineNumber++],
    char: ElementIdentifier,
    elIndex = 0;
  for (; lineNumber < seqs.length; lineNumber++) {
    for (char = prev; char < seqs[lineNumber] + 1; char++) {
      if (ecs.getWidth(char) > 0) {
        // const cursor = ecs.cursor(el);
        elements[elIndex++] = ecs.output(char);
      }
    }
    prev = char;
  }
  return elements;
}

export function create(
  input: string,
  config: ITypographyOptions,
  outputEcs: true,
): TextSequence;
export function create(
  input: string,
  config: ITypographyOptions,
  outputEcs?: false,
): ITextSequenceBase;
export function create(
  text: string,
  config: ITypographyOptions,
  outputEcs?: true | false,
): ITextSequenceBase & { elements?: element[] } {
  const len = text.length;
  ecs.grow((len > 100 ? len * 1.2 : len + 20) | 0);
  // console.time("calcTextTypography2");
  const input: string[] = tokenize(text);
  const { options, compressionOptions } =
    optionCache.get(config) || getOptions(config);
  const textSequence = createElements(input, (char) => {
    return createElm(
      char,
      // config.fontSize as i32,
      // `${config.fontSize}px ${config.fontFamily}`,
      config.fontStyle || "normal",
      config.fontVariant || "normal",
      config.fontWeight || "normal",
      config.fontSize,
      config.fontFamily,
      // config.fontSize + "px " + config.fontFamily,
      // "25px 微软雅黑",
      config.letterSpacing || 0,
    );
  });
  const ctx = calc(textSequence, options);
  // console.timeEnd("calcTextTypography2");
  if (outputEcs) {
    const elements = [] as element[];
    for (const el of visitCompressedIds(ctx.seqs)) {
      if (ecs.getWidth(el) > 0) {
        // const cursor = ecs.cursor(el);
        elements.push(ecs.output(el));
      }
    }
    // console.log(elements);
    return {
      ...ctx,
      elements: elements,
    };
  }
  return ctx;
}

export * from "./render";
export { Cache, printCache };
