import { memoize } from "lodash";
import * as pixress from "piress-core";
import * as PIXI from "pixi.js";


export const getTextStyle = memoize(
  (data: pixress.element) => {
    // console.log("createstyle");
    return new PIXI.TextStyle({
      // fontSize: data.fontSize,
      // fill: "#dddddd",
      fillGradientType: PIXI.TEXT_GRADIENT.LINEAR_VERTICAL,
      fill: ["#ffffff", "#2de2ff"], // gradient,
      stroke: "#333",
      strokeThickness: 2,
      dropShadow: true,
      dropShadowColor: "#333",
      dropShadowBlur: 2,
      dropShadowAngle: 45,
      dropShadowDistance: 2,
      fontFamily: data.style.fontFamily,
      fontSize: data.style.fontSize,
      fontStyle: data.style.fontStyle,
      fontWeight: data.style.fontWeight,
      fontVariant: data.style.fontVariant,
      letterSpacing: data.style.letterSpacing,
      // textBaseline: "",
    });
  },
  (el) => el.style.id
);
