import { action, makeAutoObservable, observable } from "mobx";
import * as pixress from "piress-core";
import { createService, events, MachineEvent, MachineState } from "./Machine";
import { State } from "./State";

const {
  [MachineEvent.NextTick]: handleNextTick,
  [MachineEvent.Confirm]: handleConfirm,
  [MachineEvent.AutoStart]: handleAutoStart,
  [MachineEvent.AutoEnd]: handleAutoEnd,
  [MachineEvent.AutoNext]: handleAutoNext,

  [MachineEvent.PlayEnd]: handlePlayEnd,
  [MachineEvent.PlayVoiceEnd]: handlePlayVoiceEnd,

  [MachineEvent.SkipStart]: handleSkipStart,
  [MachineEvent.SkipEnd]: handleSkipEnd,
} = events;

export enum Action {
  Stop = "stop",
  Run = "run",
  Wait = "wait",
}

export function* controller(datas: pixress.TextSequence[]) {
  const state = {
    id: 0,
  };
  let index = 0;
  for (;;) {
    // 等待下一个指令
    const action: Action = yield { data: datas[index] };
    if (action === Action.Run) {
      state.id++;
      index = state.id % datas.length;
    } else if (action === Action.Stop) {
      break;
    } else {
      //
    }
  }
  return datas[index];
}

export class Controller {
  currentState: MachineState = MachineState.Stop;
  enabled: boolean = false;
  state: State;
  service: ReturnType<typeof createService>;
  constructor(public datas: pixress.TextSequence[]) {
    this.state = new State(datas).init();
    this.service = createService(() => this.state);
    this.service.onTransition((o) => {
      const stateName: MachineState =
        typeof o.value === "string"
          ? o.value
          : (Object.keys(o.value)[0] as any);
      if (stateName !== this.currentState) {
        this.currentState = stateName;
        console.log("current: %o", stateName, o.value);
        if (this.currentState === MachineState.PlayStart) {
          this.next(Action.Run);
        }
      }
    });
    makeAutoObservable(this, {
      service: observable.ref,
      datas: observable.ref,
    });
  }

  cycle = () => {
    const onKeyDown = (e: KeyboardEvent) => {
      if (!this.state.skipping && e.key === "Control") {
        this.service.send(handleSkipStart(true));
        this.state.setSkiping(true);
      }
    };
    const onKeyUp = (e: KeyboardEvent) => {
      if (this.state.skipping && e.key === "Control") {
        this.service.send(handleSkipEnd());
        this.state.setSkiping(false);
      }
      if (e.key === "Shift") {
        if (this.state.auto) {
          this.service.send(handleAutoEnd());
          this.state.setAuto(false);
        } else {
          this.service.send(handleAutoStart());
          this.state.setAuto(true);
        }
      }
    };
    const onClick = () => {
      this.service.send(handleConfirm());
    };
    window.addEventListener("keydown", onKeyDown);
    window.addEventListener("keyup", onKeyUp);
    window.addEventListener("click", onClick);
    this.service.start();
    this.enabled = true;
    return action("dispose", () => {
      window.removeEventListener("keydown", onKeyDown);
      window.removeEventListener("keyup", onKeyUp);
      window.removeEventListener("click", onClick);
      this.service.stop();
      this.enabled = false;
    });
  };

  playEnd(length: number) {
    this.service.send(handlePlayEnd(length));
  }

  update() {
    this.service.send(handleNextTick());
    this.next(this.state.skipping ? Action.Run : Action.Wait);
  }

  next(action: Action) {
    if (this.enabled) {
      return this.state.next(action);
    }
  }
}
