import { Container, Sprite, Text } from "@pixi/react-animated";
import * as pixress from "piress-core";
import * as PIXI from "pixi.js";
import React from "react";
import { AnimatedProps } from "react-spring";
import { getTextStyle } from "./getTextStyle";

export const TextElement: React.FC<
  AnimatedProps<pixress.element & { alpha?: number }> & {
    source: pixress.element;
  }
> = (props) => {
  return (
    <Container alpha={props.alpha} x={props.x} y={props.y}>
      <Sprite
        alpha={0}
        texture={PIXI.Texture.WHITE}
        tint={0xeeeeee}
        width={props.width}
        height={props.height}
      ></Sprite>
      <Text
        text={props.character}
        isSprite
        style={getTextStyle(props.source)}
      />
    </Container>
  );
};
