const seqs2 = pixress.create(
  text2,
  {
    fontSize: 35,
    fontFamily: "华文仿宋",
    wrapWidth: 790,
    fontStyle: "normal",
    fontWeight: "bold",
    fontVariant: "normal",
    // xInterval: 5,
    letterSpacing: 5,
  },
  true,
);

console.log(seqs1, seqs2);

const config: pixress.ITypographyOptions = {
  fontSize: 35,
  fontFamily: "华文仿宋",
  wrapWidth: 790,
  fontStyle: "normal",
  fontWeight: "bold",
  fontVariant: "normal",
  // xInterval: 5,
  letterSpacing: 5,
};
const seqs = generateMockData(10000, true).map((txt) =>
  pixress.create(txt, config, true),
);
const controller = new Controller(seqs);
const TextViewDemo = () => {
  React.useEffect(controller.cycle, []);
  // useLocalObservable(() => ({ font }));
  useTick((delta, ticker) => controller.update());
  return useObserver(() => {
    const { skipping, current: datas, playEnd } = controller.state;
    return (
      <Container>
        <Text text={controller.currentState}></Text>
        <Container y={50}>
          {skipping || playEnd ? (
            <PureTextRenderer datas={datas} />
          ) : (
            <TextView
              datas={datas}
              onPlayEnd={(datas) => {
                controller.playEnd(datas.length);
              }}
            />
          )}
        </Container>
      </Container>
    );
  });
};

export default () => {
  return (
    <Stage
      options={{
        backgroundColor: 0xffffff,
      }}
      raf
      renderOnComponentChange
      width={1280}
      height={720}
    >
      <TextViewDemo />
      {/* <TextDemo seqs={seqs[10]}></TextDemo> */}
    </Stage>
  );
};

pixress.printCache();
