import { makeAutoObservable, observable } from "mobx";
import * as pixress from "piress-core";
import { Action, controller } from "./Controller";

export class State {
  config = {
    /**
     * 启用自动播放时，先等待一段时间
     */
    delayWhenAutoStart: false,
    noVoice: true,
  };

  skipping = false;

  auto = false;

  done = false;

  playEnd = false;

  delay: number = 1000;

  currentNoVoice = false;
  get noVoice() {
    return this.currentNoVoice || this.config.noVoice;
  }

  current!: pixress.ITextSequenceBase;
  constructor(public datas: pixress.TextSequence[], public id: number = 0) {
    this.current = datas[id];
    this.gen = controller(datas);
  }

  setSkiping(skipping: boolean) {
    this.skipping = skipping;
  }

  setAuto(auto: boolean) {
    this.auto = auto;
  }

  setPlayEnd(playEnd: boolean) {
    this.playEnd = playEnd;
  }

  setCurrent(next: pixress.TextSequence, noVoice?: boolean) {
    this.current = next;
    if (arguments.length === 2) {
      this.currentNoVoice = noVoice || false;
    }
  }

  setDone() {
    this.done = true;
  }

  init() {
    makeAutoObservable(this, {
      current: observable.ref,
      config: observable.ref,
      gen: observable.ref,
      datas: observable.ref,
    });
    return this;
  }

  increment() {
    this.id++;
  }
  setId(id: number) {
    this.id = id;
  }

  gen: ReturnType<typeof controller>;

  next(action: Action) {
    const res = this.gen.next(action);
    const { done, value } = res;
    if (!done) {
      if (value.data !== this.current) {
        this.setCurrent(value.data);
        // console.log(r.value);
      }
    } else {
      this.setDone();
    }
    return res;
  }
}
