import { Container } from "@pixi/react-animated";
import * as pixress from "piress-core";
import React from "react";
import { useTransition, UseTransitionProps } from "react-spring";
import { TextRenderer } from "./TextRenderer";

export function TextView(
  props: {
    datas: pixress.ITextSequenceBase;
    skipping?: boolean;
    onPlayEnd: (datas: pixress.element[]) => any;
  } & UseTransitionProps<pixress.ITextSequenceBase>,
) {
  const textId = React.useRef(0);
  const {
    skipping,
    datas,
    reverse,
    from,
    enter,
    leave,
    config,
    onPlayEnd,
    ...other
  } = props;
  // const api = useSpringRef();
  const transition = useTransition<
    pixress.ITextSequenceBase,
    { alpha: number }
  >(
    [datas],
    React.useMemo(
      () => ({
        ...other,
        // ref: api,
        from: { alpha: 0, ...(from as any) },
        enter: { alpha: 1, ...(enter as any) },
        leave: { alpha: 0, ...(leave as any) },
        config: {
          duration: 100,
          ...config,
        },
        exitBeforeEnter: false,
      }),
      [from, enter, leave, config],
    ),
  );
  const elements = React.useMemo(() => pixress.output(datas), [datas]);
  React.useEffect(() => {
    textId.current %= 20;
    datas.id = textId.current++;
  }, [datas]);
  // React.useEffect(() => {
  //   if (!props.skipping) {
  //     const flag =  requestAnimationFrame(() => {
  //       api.start();
  //       console.log("play")
  //       // api.current.forEach((e) => console.log(e));
  //     });
  //     return () => cancelAnimationFrame(flag)
  //   }
  // }, [datas, props.skipping]);
  const onRest = React.useCallback<
    Extract<UseTransitionProps<pixress.element>["onRest"], Function>
  >(
    (_res, _ctx, item) => {
      if (item === elements[elements.length - 1]) {
        // console.log(item);
        onPlayEnd(elements);
      }
    },
    [datas],
  );
  return transition((props, data) => {
    return (
      <Container key={data.id} {...props}>
        <TextRenderer datas={elements} onRest={onRest} />
      </Container>
    );
  });
}
