import { Container } from "@pixi/react-animated";
import * as pixress from "piress-core";
import React from "react";
import { useSpringRef, useTransition, UseTransitionProps } from "react-spring";
import { extendProps } from "./extendProps";
import { TextElement } from "./TextElement";

export function TextRenderer(
  props: {
    datas: pixress.element[];
  } & UseTransitionProps<pixress.element>,
) {
  const played = React.useRef(true);
  const api = useSpringRef();
  const { datas, reverse, from, enter, leave, config, ...other } = props;
  const transition = useTransition(datas, {
    ref: api,
    trail: 10,
    from: { alpha: 0, x: -20, ...from },
    enter: { alpha: 1, x: 0, ...enter },
    leave: { alpha: 0, x: 20, ...leave },
    config: {
      duration: 120,
      ...config,
    },
    exitBeforeEnter: false,
    ...other,
  });
  React.useEffect(() => {
    const flag = requestAnimationFrame(() => {
      api.start();
      played.current = true;
      // api.current.forEach((e) => console.log(e));
    });
    return () => {
      cancelAnimationFrame(flag);
    };
  }, [datas]);
  return (
    <Container>
      {transition((props, data) => {
        const extended = extendProps(data, ["x", "y", "alpha"], props);
        // console.log(extended);
        return (
          //       <Container
          //       key={datas.indexOf(data)} alpha={extended.alpha} x={extended.x} y={extended.y}>
          //   {/* <Sprite
          //     texture={PIXI.Texture.WHITE}
          //     tint={0xeeeeee}
          //     width={props.width}
          //     height={props.height}
          //   ></Sprite> */}
          //   <Text
          //     text={data.character}
          //     isSprite
          //     style={getTextStyle(data)}
          //   />
          // </Container>
          <TextElement
            key={datas.indexOf(data)}
            {...data}
            {...extended}
            source={data}
          />
        );
      })}
    </Container>
  );
}
