import { applyDefaultProps, PixiComponent } from "@pixi/react";
import * as pixress from "piress-core";
import * as PIXI from "pixi.js";
import React from "react";

export const PureTextRenderer = PixiComponent<
  {
    datas: pixress.ITextSequenceBase;
  },
  PIXITextViewerClass & PIXI.DisplayObject
>("PIXITextViewer", {
  create(props) {
    const instance = new PIXITextViewerClass();
    // instance.updateSeqs(props.seqs);
    instance.makeObservable();
    return instance;
  },
  applyProps(instance, oldProps, newProps) {
    if (oldProps.datas !== newProps.datas) {
      // console.log("update")
      instance.updateSeqs(newProps.datas);
    }
    applyDefaultProps(instance, oldProps, newProps);
  },
  willUnmount(instance, parent) {
    // instance.destroy({ children: true, texture: true, baseTexture: true });
    // instance.helper.worker.terminate();
  },
}) as React.FC<
  {
    datas: pixress.ITextSequenceBase;
  } & {
    ref?: React.Ref<PIXITextViewerClass & PIXI.DisplayObject> | undefined;
  }
>;
