  seqs: pixress.ITextSequenceBase = null!;

  bitmap!: ImageBitmap;
  texture!: PIXI.Texture;
  textContent!: PIXI.Sprite;

  async load(seqs: pixress.ITextSequenceBase) {
    const padding = 5;
    this.bitmap = pixress.offscreenRender({
      // debug: true,
      seqs,
      width: seqs.width,
      height: seqs.height,
      padding,
      effect: true,
      shadow: true, //"fake",
      // requestId: this.index,
      // style: this.store.textStyle,
      style: {
        // background: "#ffffff",
        // fill: "#dddddd",
      },
    });
    this.textContent = new PIXI.Sprite(
      (this.texture = PIXI.Texture.from(this.bitmap, {
        pixiIdPrefix: "p",
      }))
    );
    this.textContent.y = -6;
    this.textContent.x = -6;
    this.removeChildren(0, this.children.length);
    this.addChild(this.textContent);
    // console.log("loaded", this.index);
  }
  destroy() {
    super.destroy({ children: true, texture: true, baseTexture: true });
    this.seqs = null!;
    this.texture?.destroy(true);
    this.bitmap?.close();
    // this.helper.destory();
  }
