import {
  Interpolation,
  SpringValue
} from "react-spring";

export function extendProps<
  R extends Record<string, any>,
  K extends keyof R | (string & {}),
  E extends Partial<Record<K, SpringValue<number>>>
>(props: R, keys: K[], extend: E) {
  const result = {} as {
    [Key in K]: Key extends keyof E ? Interpolation<number, number> : Key extends keyof R ? R[Key] : E[Key];
  };
  for (const key of keys) {
    if (extend[key]) {
      result[key] =
        typeof props[key] === "number"
          ? (extend[key]!.to((value: number) => props[key] + value) as any)
          : extend[key];
    } else if (typeof props[key] === "number") {
      result[key] = props[key];
    }
  }
  return result;
}
