import { actions, interpret, StatesConfig } from "xstate";
import { createModel } from "xstate/lib/model";
import { State } from "./State";
const { send, cancel } = actions;

export enum MachineEvent {
  _Init = "xstate.init",
  NextTick = "NextTick",
  Confirm = "Confirm",
  AutoStart = "AutoStart",
  AutoEnd = "AutoEnd",
  AutoNext = "AutoNext",

  PlayEnd = "PlayEnd",
  PlayVoiceEnd = "PlayVoiceEnd",

  SkipStart = "SkipStart",
  SkipEnd = "SkipEnd",
}

export enum MachineState {
  PlayStart = "PlayStart",
  Skipping = "Skipping",
  ForceSkipping = "ForceSkipping",
  Waiting = "Waiting",
  Playing = "Playing",
  WaitingWithAuto = "WaitWithAuto",
  Stop = "Stop",
}

const model = createModel({} as State, {
  events: {
    [MachineEvent.NextTick]: () => ({}),
    [MachineEvent.Confirm]: () => ({}),
    [MachineEvent.AutoStart]: () => ({}),
    [MachineEvent.AutoEnd]: () => ({}),
    [MachineEvent.AutoNext]: () => ({}),

    [MachineEvent.PlayEnd]: (length: number) => ({ length }),
    [MachineEvent.PlayVoiceEnd]: () => ({}),

    [MachineEvent.SkipStart]: (force: boolean) => ({ force }),
    [MachineEvent.SkipEnd]: () => ({}),
  },
});

function createPendingState(resolveName: string, cond?: string) {
  return {
    initial: "playing",
    states: {
      playing: {
        on: {
          [MachineEvent.NextTick]: cond ? { cond, target: "end" } : undefined,
          [resolveName]: {
            target: "end",
            actions: (ctx, a, b) => {
              console.log(ctx, a, b);
            },
          },
        },
      },
      end: { type: "final" },
    } as StatesConfig<any, any, any>,
  };
}

enum MachineAction {
  onPlayEnd = "PlayEnd",
  onPlayStart = "PlayStart",
  onProcessVoice = "ProcessVoice",
}
enum MachineGuards {
  NoVoice = "noVoice",
  HasVoice = "hasVoice",
  IsAuto = "isAuto",
  DelayWhenAutoStart = "delayWhenAutoStart",
}
const machine = model.createMachine(
  {
    id: "process",
    initial: MachineState.PlayStart,
    states: {
      [MachineState.PlayStart]: {
        entry: [MachineAction.onPlayStart],
        on: {
          [MachineEvent.NextTick]: {
            target: MachineState.Playing,
            actions: [MachineAction.onProcessVoice],
          },
        },
      },
      [MachineState.Playing]: {
        type: "parallel",
        states: {
          text: {
            initial: "playing",
            states: {
              playing: {
                on: {
                  [MachineEvent.PlayEnd]: {
                    target: "end",
                    actions: (ctx, event) => {
                      ctx.delay = event.length * 3;
                    },
                  },
                },
              },
              end: { type: "final" },
            },
          },
          voice: createPendingState(
            MachineEvent.PlayVoiceEnd,
            MachineGuards.NoVoice,
          ),
        },
        onDone: [
          {
            cond: MachineGuards.IsAuto,
            target: MachineState.WaitingWithAuto,
          },
          { target: MachineState.Waiting },
        ],
        on: {
          [MachineEvent.Confirm]: [
            {
              cond: MachineGuards.IsAuto,
              target: MachineState.WaitingWithAuto,
            },
            { target: MachineState.Waiting },
          ],
        },
      },
      [MachineState.Waiting]: {
        entry: [MachineAction.onPlayEnd],
        on: {
          [MachineEvent.Confirm]: MachineState.PlayStart,
          [MachineEvent.AutoStart]: [
            {
              cond: MachineGuards.DelayWhenAutoStart,
              target: MachineState.WaitingWithAuto,
            },
            {
              target: MachineState.PlayStart,
            },
          ],
        },
      },
      [MachineState.WaitingWithAuto]: {
        entry: [
          MachineAction.onPlayEnd,
          send(
            {
              type: MachineEvent.Confirm,
              cond: MachineGuards.NoVoice,
            },
            {
              // 延迟由自定义 event.wait 属性确定
              delay: (context, event) => 700 + context.delay,
              id: MachineAction.onPlayEnd,
            },
          ),
        ],
        exit: [cancel(MachineAction.onPlayEnd)],
        on: {
          [MachineEvent.NextTick]: {
            cond: MachineGuards.HasVoice,
            target: MachineState.PlayStart,
          },
          [MachineEvent.Confirm]: MachineState.PlayStart,
          [MachineEvent.AutoEnd]: [
            {
              actions: [cancel(MachineAction.onPlayEnd)],
              target: MachineState.Waiting,
            },
          ],
        },
      },
      [MachineState.Skipping]: {
        on: {
          [MachineEvent.Confirm]: MachineState.PlayStart,
          [MachineEvent.SkipEnd]: MachineState.PlayStart,
        },
      },
      [MachineState.ForceSkipping]: {
        on: {
          [MachineEvent.SkipEnd]: MachineState.PlayStart,
        },
      },
    },
    on: {
      [MachineEvent.SkipStart]: [
        { cond: (_, e) => e.force, target: MachineState.ForceSkipping },
        { target: MachineState.Skipping },
      ],
    },
    predictableActionArguments: true,
  },
  {
    actions: {
      [MachineAction.onProcessVoice](context: State, source, meta) {
        console.log("ProcessVoice", source.type, meta);
      },
      [MachineAction.onPlayStart](context: State) {
        context.setPlayEnd(false);
      },
      [MachineAction.onPlayEnd](context: State, source, meta) {
        // console.log("PlayEnd", source, meta);
        context.setPlayEnd(true);
      },
    },
    guards: {
      [MachineGuards.NoVoice](context: State) {
        // console.log("has no Voice");
        return context.noVoice;
      },
      [MachineGuards.HasVoice](context: State) {
        // console.log("has Voice");
        return !context.noVoice;
      },
      [MachineGuards.IsAuto]: (context: State) => {
        // console.log("isAuto", context.auto);
        return context.auto;
      },
      [MachineGuards.DelayWhenAutoStart]: (context: State) =>
        context.config.delayWhenAutoStart,
    },
  },
);

export const { events } = model;

export function createService(state: () => State) {
  return interpret(machine.withContext(state));
}
export type Service = ReturnType<typeof createService>;
